.cart-container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.cart-title {
    text-align: center;
    font-size: 2rem;
    color: #333;
    margin-bottom: 20px;
}

.cart-items {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.cart-item {
    display: flex;
    align-items: center;
    background-color: #fff;
    padding: 15px;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.cart-item-image {
    width: 100px;
    height: auto;
    border-radius: 5px;
    margin-right: 20px;
}

.cart-item-info {
    flex-grow: 1;
}

.quantity-input {
    width: 50px;
    padding: 5px;
    margin-left: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
}

.remove-button {
    background-color: #ff4d4d;
    color: white;
    border: none;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
}

.remove-button:hover {
    background-color: #ff1a1a;
}

.cart-total {
    text-align: right;
    font-size: 1.5rem;
    margin-top: 20px;
    color: #333;
}

.empty-cart {
    text-align: center;
    font-size: 1.2rem;
    color: #999;
}
